import { useTranslation } from 'react-i18next';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';

import GroupAddIcon from '@material-ui/icons/GroupAdd';
import HomeIcon from '@material-ui/icons/Home';

import Link from './Link';

import { toolbarStyle } from './styles';

const LIST_ITEMS_1 = [
  {
    Icon: () => <GroupAddIcon />,
    textKey: 'screens:socialLinks.title',
    href: '/social-links',
  },
];

const LIST_ITEMS_2 = [
  {
    Icon: () => <HomeIcon />,
    textKey: 'screens:home.title',
    href: '/',
  },
];

const useStyles = makeStyles((theme) => ({
  ...toolbarStyle(theme),

  drawer: {
    flexShrink: 0,
    whiteSpace: 'nowrap',
  },
  drawerOpen: (props) => ({
    width: props.drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
  drawerClose: {
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9) + 1,
    },
  },
}));

export default function MiniDrawer(props) {
  const { drawerWidth, open, handleDrawerClose } = props;
  const classes = useStyles({ drawerWidth });
  const theme = useTheme();
  const { t } = useTranslation('screens');

  return (
    <Drawer
      variant="permanent"
      className={clsx(classes.drawer, {
        [classes.drawerOpen]: open,
        [classes.drawerClose]: !open,
      })}
      classes={{
        paper: clsx({
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        }),
      }}
    >
      <div className={classes.toolbar}>
        <IconButton onClick={handleDrawerClose}>
          {theme.direction === 'rtl' ? (
            <ChevronRightIcon />
          ) : (
            <ChevronLeftIcon />
          )}
        </IconButton>
      </div>
      <Divider />
      <List>
        {LIST_ITEMS_1.map((item) => (
          <Link
            color="inherit"
            href={item.href}
            key={item.textKey}
            style={{ textDecoration: 'none' }}
          >
            <ListItem button>
              <ListItemIcon>
                <item.Icon />
              </ListItemIcon>
              <ListItemText primary={t(item.textKey)} />
            </ListItem>
          </Link>
        ))}
      </List>
      <Divider />
      <List>
        {LIST_ITEMS_2.map((item) => (
          <Link color="inherit" href={item.href} key={item.textKey}>
            <ListItem button key={item.textKey}>
              <ListItemIcon>
                <item.Icon />
              </ListItemIcon>
              <ListItemText primary={t(item.textKey)} />
            </ListItem>
          </Link>
        ))}
      </List>
    </Drawer>
  );
}
